:root {
  --cool-grey: #90a19c;
  --faded-orange: #e49e52;
  --white: #ffffff;
  --faded-red: #d74247;
  --cloudy-blue: #cce1db;
  --steel-grey: #72837e;
}

.header {
  padding-top: 30px;
}

.header .header__home {
  max-width: 300px;
}

.header .navigation {
  flex-grow: 1;
  text-align: right;
  padding-top: 30px;
}

.header .navigation__list {
  margin: 0;
  padding: 0;
}

.header .navigation__list-item {
  display: inline-block;
}

.header .navigation__item {
  display: inline-block;
  padding: 15px 10px;
  color: #72837e;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.2s;
}

.header .navigation__item:hover, .header .navigation__item:focus {
  color: #d74247;
}

.footer {
  background: #90a19c;
  color: white;
  padding: 50px 0;
}

.footer .wrapper {
  flex-wrap: wrap;
}

.footer a {
  color: white;
}

.footer .footer-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 50%;
}

@media only screen and (min-width: 768px) {
  .footer .footer-links {
    width: 25%;
  }
}

.footer .footer-links .footer-nav__list-item,
.footer .footer-links .legal-nav__list-item {
  line-height: 1.3;
}

.footer .footer-links .footer-nav__item,
.footer .footer-links .legal-nav__item {
  font-size: 12px;
}

.footer .footer-links .footer-nav__item {
  font-weight: bold;
}

.footer .footer-disclaimer {
  padding-top: 25px;
}

@media only screen and (min-width: 768px) {
  .footer .footer-disclaimer {
    width: 50%;
    padding-top: 0;
  }
}

.footer .footer-disclaimer p {
  color: white;
  font-size: 10px;
  margin: 0;
}

.footer .footer-disclaimer p + p {
  margin-top: 10px;
}

.footer .footer-disclaimer a {
  text-decoration: underline;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #d74247;
  transition: all 0.2s;
}

a:hover, a:focus {
  text-decoration: underline;
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
}

body {
  background: white url(../images/dc-background.jpg);
  background-size: 100% auto;
  background-repeat: no-repeat;
}

p,
ul,
ol {
  font-weight: 300;
  line-height: 1.5;
  letter-spacing: -0.3px;
  font-size: 14px;
  color: #546560;
  font-weight: 300;
}

p {
  margin: 0;
}

p + p {
  margin-top: 25px;
}

.hero {
  height: 33vw;
  min-height: 300px;
  max-height: 500px;
  position: relative;
  margin-top: 20px;
}

.hero.wrapper {
  padding-right: 0;
  padding-left: 0;
}

.hero.hero-about {
  background: url(../images/hero-images/hero-about.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}

.hero.hero-parents {
  background: url(../images/hero-images/hero-parents.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}

.hero.hero-community {
  background: url(../images/hero-images/hero-community.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}

.hero.hero-primary-care {
  background: url(../images/hero-images/hero-primary-care.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}

.hero.hero-partners {
  background: url(../images/hero-images/hero-partners.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}

.hero.hero-research {
  background: url(../images/hero-images/hero-research.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}

.hero.hero-acquire {
  background: url(../images/hero-images/hero-acquire.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}

.hero.hero-contact {
  background: url(../images/hero-images/hero-contact.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}

.hero .hero__title {
  position: absolute;
  left: -20px;
  bottom: 30px;
  background: var(--faded-orange);
  width: 300px;
  padding: 18px 50px 24px;
  color: white;
  font-size: 36px;
  font-weight: normal;
  line-height: 1.12;
  letter-spacing: -1px;
  margin: 0;
}

@media only screen and (min-width: 768px) {
  .content-wrapper {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

.chapter-nav {
  display: none;
}

@media only screen and (min-width: 768px) {
  .chapter-nav {
    width: 25%;
    max-width: 240px;
    display: block;
    padding: 15px;
    line-height: 1.7;
    list-style: none;
  }
}

.chapter-nav .chapter-nav__item {
  opacity: 0.5;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: -0.3px;
  color: var(--cool-grey);
}

.chapter-nav .chapter-nav__item.active {
  color: #d74247;
}

.page-content {
  flex-grow: 1;
}

@media only screen and (min-width: 768px) {
  .page-content {
    max-width: 800px;
    width: 67%;
  }
}

.page-content p + .content-heading,
.page-content ul + .content-heading {
  padding-top: 50px;
}

.content-heading {
  font-size: 21px;
  font-weight: bold;
  line-height: 1.21;
  letter-spacing: -0.3px;
  color: var(--faded-red);
}
